<template>
	<section class="problem">
		<!-- <div class="forgetHead">
			<img src="../images/back_aw.png" @click="history"/>
			<span>常见问题</span>
		</div> -->
		<el-collapse>
		  	<el-collapse-item :title="item.problem_name" :name="item.sort" v-for="item in questionList" :key="item.id">
		    	<div v-for="items in item.problemGeneralDetails">{{ items.small_sort +'、'+ items.problem_answer }}</div>
		  	</el-collapse-item>
		</el-collapse>
	</section>
</template>
<script>
import { getQuestion } from '../api/api'
export default {
	data () {
    	return {
    		questionList: []
    	}
	},
	methods: {
  // 		history() { // 后退
		// 	this.$router.push({path: '/mine'})
		// },
		init() {
			getQuestion().then(res => {
				if (res.code === 200) {
					this.questionList = res.data
				}
			})
		}
	},
	computed: {
  		
  	},
  	mounted() {
  		this.init()
  	}
}
</script>

<style lang="scss" rel="stylesheet/scss">
.problem {
	flex:1;
	z-index: 11;
	background: #f3f5f8;
	
	// .forgetHead {
	// 	width: 100%;
	// 	background: white;
	// 	position: relative;
	// 	height: 0.8rem;
	// 	line-height: 0.8rem;
		
	// 	img {
	// 		vertical-align: middle;
	// 		width: 0.15rem;
	// 		margin-left: 0.2rem;
	// 	}
	// 	span:nth-child(2) {
	// 		vertical-align: middle;
	// 		font-size: 0.25rem;
	// 		color: black;
	// 		display: inline-block;
	// 		width: 90%;
	// 		text-align: center;
	// 	}
	// }
	
	.el-collapse {
		
		.el-collapse-item__header {
			color: black;
			font-size: 0.3rem;
			height: 0.6rem;
			line-height: 0.6rem;
			border: 0;
			margin-top: 0.2rem;
			
			.el-collapse-item__header__arrow {
				margin-right: 0.2rem;
			}
			.el-icon-arrow-right:before {
				
			}
		}
		.el-collapse-item__wrap {
			border: 0;
		}
		.el-collapse-item__content {
			background: white;
			color: #959595;
			font-size: 0.3rem;
			padding: 0.1rem 0.2rem 0.2rem 0.65rem;
			
		}
		
		.el-collapse-item.is-active>.el-collapse-item__header .el-collapse-item__header__arrow {
			color: red;
		}
	}
}
</style>



